/* Configuraciones */
:root {
  --blanco: #ffff;
  --gris: #cccc;
  --negro: rgb(0, 0, 0.5);
  --sombra: 3px 5px 2px 0px rgba(55, 46, 48, 0.17);

  --azulTemplate: #303c54;
  --azulTemplate2: #324364;

  --azul1: #2d2a8c;
  --azul2: #303C54;
  --azul3: #3365a6;
  --azul4: #368abf;
  --azul5: #48b0d9;
}

.contenedor-cabecera {
  background-color: var(--azulTemplate) !important;
  color: var(--blanco);
  font-size: 0.8rem;
}

.contenedor-cabecera-principal {
  background-color: var(--azulTemplate);
  color: var(--blanco);
  font-size: 1rem;
}

.contenedor-cabecera-grafica {
  background-color: var(--blanco);
  color: var(--azulTemplate);
  font-size: 0.8rem;
}

.icono {
  width: 48px;
  height: 48px;
}

pre {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

li {
  font-size: 0.9rem;
}
